	module.exports = {"data":{"markdownRemark":{"html":"<p>The end of the year has been sneaking up on me. I realized that I’ve been seriously neglecting my Free Code Camp practice. Seeing as how I had some free time on my hands, in between taking a pretty good, in-depth beginner course on D3 JS, I decided to tackle my next Front End Project: the pomodoro timer.</p>\n<h3>The Pitch</h3>\n<p>So the project was to reverse-engineer this <a href=\"http://codepen.io/GeoffStorbeck/full/RPbGxZ/\">pen by Geoff Stoerbeck</a>. Seems simple right? Wrong! I could tell right from the beginning that this project was going to be more complex than the others had been, which is probably why I dragged my feet on it. But once I did get started, I found myself super motivated to work through the problems my idea presented.</p>\n<h3>My Idea</h3>\n<p>I took my inspiration both from the pen and from pomodoro apps in the iOS App Store, <a href=\"https://itunes.apple.com/us/app/pomodoro-timer-focus-on-your/id703145045?mt=8\">particularly this offering by Nasa Trained Monkeys</a>. Essential functionality was to allow users to set both the session and break time and start the timer. They should also be able to pause the timer and restart it. On the aesthetic side, I really liked the idea of filling up the circle as time passed in the reference pen, but I wanted to add on to that idea by creating little progress bars for each session. I also really liked the idea of the progress bar shaped like a tomato. With those two things in the back of my mind, I resolved on beginning at the beginning and just get something working.</p>\n<figure>\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 320px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 177.5%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Inspriration Pomodoro App\" title=\"\" src=\"/static/pomodoro-timer-app-6d499a13ce0ae7591c72c08062df39a7-191e8.jpeg\" srcset=\"/static/pomodoro-timer-app-6d499a13ce0ae7591c72c08062df39a7-63e2d.jpeg 203w,\n/static/pomodoro-timer-app-6d499a13ce0ae7591c72c08062df39a7-191e8.jpeg 320w\" sizes=\"(max-width: 320px) 100vw, 320px\">\n    </span>\n  </span>\n  \n</figure>\n<h3>Finding Plugins</h3>\n<p>My first trial was to find some frameworks that would allow me to do what I wanted. Naturally, I started with basic functionality. I needed something that would let me easily build a timer. There’s no shortage of timer plugins for jQuery but I was able to sift through them fairly easily, eschewing anything that was pre-styled. I experimented with <a href=\"http://csouza.me/jQuery-Simple-Timer/\">jQuery Simple Timer by Carlous Souza</a>. It has a lot of the functionality I wanted but didn’t have the built in ability to pause and restart the timers. So I kept looking until I ran across <a href=\"http://jchavannes.com/jquery-timer/demo\">Jason Chavannes’ jQuery Timer</a>, which had everything I wanted and good demos and documentation. Using Jason’s plugin, I was able to get a basic timer working fairly quickly. My structure mimics the original pen, with two variables, one for break time and one for session time, that you can modify using plus and minus buttons. Once you’ve made your selections, you click the start button and the session timer starts. Once the session timer is complete, then that div hides and the break div shows and the break timer starts.</p>\n<h3>Progress Bars</h3>\n<p>My next project was to try and add in progress bars. As I said, I originally wanted to try and create one shaped like a tomato. I hoped that my newfound beginner knowledge of svg could come in handy and I even went to the trouble to create a handy little svg tomato. (Trust me, it’s super cute.) But I abandoned that idea after a couple of days in favor of just getting a simple circle to work. I had to do a little thinking to realize that I wanted this image to act as a progress bar, but once I hit upon the idea, I went searching for another jQuery plugin. After going through a couple of options, I found that<a href=\"https://github.com/kottenator\"> Rostyslav Bryzgunov’s jQuery Circle Progress plugin fit the bill perfectly</a>. The plugin makes it super easy to create a progress bar to stand alone, but I needed it to work within my larger app. I needed to be able to start it, pause it, and resume it. Again, this was where I had a lot of issues. Documentation for the plugin mentions a way to stop the animation and, through days of experimentation, I was becoming more and more familiar with the variables involved. I was able to create a half-solution by updating the animationProgress property of the animation call. However, I found that that this only worked for the first pause. If you were to resume the timer, let it run, pause it, and try to resume it again, animationProgess stayed stuck at the older value. After about 3 days, I broke down and asked on StackOverflow. <a href=\"http://stackoverflow.com/questions/34271707/canvas-animation-progress\">As is often the way, I got a quick answer that did the trick</a>. So, 89 versions in, I was finally able to add the progress bar to my app.</p>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 37.5%; position: relative; height: 0; overflow: hidden;\"\n          >\n            <iframe src=\"http://jsfiddle.net/anthkris/jeycb92x/116/\" frameborder=\"0\" scrolling=\"no\" allowfullscreen=\"allowfullscreen\" style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe>\n          </div>\n          </div>\n<h3>The Hitch</h3>\n<p>There always has to be a problem, right? In this project, my big problem was uncovered when I realized that, while my progress bar kept right on ticking, whether or not the window tab was active, the timer was built in such a way that the browser paused its progress when you weren’t looking at it. Not fun. I was looking around for a way to fix it when I moseyed back over to the timer github repo and saw that other folks had brought up this issue and the code author had begun working on a solution! It would mean using a non-canonical branch of the code but, I was game. I refactored my code to use the new version. I had to add back in some features that had been removed, most notably the isActive property, and I also had to figure out how to update the new countdown parameter, but in the end, I got it running smoothly after about 5 or 6 hours of work.</p>\n<h3>Polishing Up</h3>\n<p>I added a few aesthetic bits at the end, fonts and styling. I was also able to add back in my super sweet tomato, albeit as a png instead of an svg. One thing that I discovered is that the code is not quite as DRY as I might like. The way the functions are written, they can’t access global variables so I’ve had to make variables more than once, within specific scopes. However, I think the code is still pretty readable. I also took the time to comment the code to point out areas of interest. This project was the most difficult so far, but I’m proud of the outcome. Probably the best thing about the method Free Code Camp chose for these projects is that they aren’t just going step by step through a tutorial. You have a project with functionality for reference but you have to figure out how to make things work on your own. At the end of it, you really feel a sense of accomplishment, plus you’ve actually put your new knowledge to work. You also get a nifty portfolio piece!</p>\n<h3>Have a Look</h3>\n<p>Enough of my rambling. Have a look at the final version and let me know what you think!</p>\n<figure>\n  <a href=\"http://codepen.io/anthkris/full/MaNZWQ/\" target=\"blank\">\n    \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 80.15414258188825%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Final timer UI\" title=\"\" src=\"/static/Screen-Shot-2015-12-16-at-9.02.05-AM-08c4ed804cfba595cebd66231a2fe346-1626e.png\" srcset=\"/static/Screen-Shot-2015-12-16-at-9.02.05-AM-08c4ed804cfba595cebd66231a2fe346-076ec.png 203w,\n/static/Screen-Shot-2015-12-16-at-9.02.05-AM-08c4ed804cfba595cebd66231a2fe346-7ae34.png 405w,\n/static/Screen-Shot-2015-12-16-at-9.02.05-AM-08c4ed804cfba595cebd66231a2fe346-1626e.png 810w,\n/static/Screen-Shot-2015-12-16-at-9.02.05-AM-08c4ed804cfba595cebd66231a2fe346-fc86f.png 1038w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n  </a>\n  <figcaption>Click to see the timer!</figcaption>\n</figure>","frontmatter":{"date":"December 16, 2015","path":"/blog/december-gds-project-pomodoro-timer","title":"December GDS Project: Pomodoro Timer","tags":["app","free code camp","pomodoro","programming","timer"],"categories":["Coding","GDS Challenge","Showing my Work"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":2,"src":"/static/December-GDS-Project-Pomodoro-Timer-e7496ebef861176f1c39a16eb774d2fe-81d39.png","srcSet":"/static/December-GDS-Project-Pomodoro-Timer-e7496ebef861176f1c39a16eb774d2fe-9a8d1.png 203w,\n/static/December-GDS-Project-Pomodoro-Timer-e7496ebef861176f1c39a16eb774d2fe-c53f9.png 405w,\n/static/December-GDS-Project-Pomodoro-Timer-e7496ebef861176f1c39a16eb774d2fe-81d39.png 810w,\n/static/December-GDS-Project-Pomodoro-Timer-e7496ebef861176f1c39a16eb774d2fe-fda19.png 1024w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

